.vmc-toast .block{position:fixed;max-width:80%;-webkit-border-radius:5px;border-radius:5px;background:rgba(0,0,0,0.7);color:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;text-align:center;z-index:500;-webkit-transition:opacity .3s linear;transition:opacity .3s linear;padding:10px}.vmc-toast .block.block-top{top:50px;left:50%;-webkit-transform:translate(-50%, 0);-ms-transform:translate(-50%, 0);transform:translate(-50%, 0)}.vmc-toast .block.block-center{left:50%;top:50%;-webkit-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.vmc-toast .block.block-bottom{bottom:50px;left:50%;-webkit-transform:translate(-50%, 0);-ms-transform:translate(-50%, 0);transform:translate(-50%, 0)}.vmc-toast .block .icon{font-size:30px;text-align:center}.vmc-toast .block .icon>div{margin:0 auto}.vmc-toast .block .text{display:block;font-size:14px;text-align:center}.vmc-toast-enter,.vmc-toast-leave{opacity:0}


