//*-- Forbidden --*\\
.icono-forbidden{
    .square(@28);
    &:extend(.icono-stroke);
    border-width: @3;
    border-radius: 50%;
    margin: @3;
    transform: rotate(45deg);
    &:before{
        &:extend(.stickCenter);
        width: @24;
        height: @4;
        box-shadow: inset 0 0 0 @32;
    }
}


