//*-- Headphone --*\\
.icono-headphone{
    width: @30;
    height: @27;
    &:extend(.icono-stroke);
    border-bottom-color: transparent;
    border-radius: @32~'/'@32 @32 @16 @16;
    margin: @2 @2 @5;
    &:before{
        position: absolute;
        width: @4;
        height: @12;
        left: @1;
        bottom: -1*@4;
        border-radius: @5;
        box-shadow: inset 0 0 0 @32, @20 0 0 0;
    }
}


