//*-- Sign out --*\\
.icono-signOut{
    width: @18;
    height: @32;
    &:extend(.icono-stroke);
    border-right: none;
    margin-right: @5;
    border-radius: @3 0 0 @3;
    margin: @1 @8;
    &:before{
        position: absolute;
        width: @11;
        height: @11;
        right:-1*@2;
        top:@7;
        &:extend(.icono-stroke);
        border-bottom: none;
        border-left: none;
        transform: rotate(45deg);
        border-radius: 0 @4 0 0;    
    }
}


