//*-- Moon --*\\
.icono-moon{
    width: @22;
    height: @22;
    border-radius: 50%;
    overflow: hidden;
    margin: @6;
    &:before{
        position: absolute;
        width: @20;
        height: @20;
        top: -@2;
        left: @6;
        border-radius: 50%;
        box-shadow: 0 0 0 @32;
    }
}


