//*-- Sign in --*\\
.icono-signIn{
    width: @18;
    height: @32;
    &:extend(.icono-stroke);
    border-left: none;
    margin-left: @5;
    border-radius: 0 @3 @3 0;
    margin: @1 @8;
    &:before{
        position: absolute;
        width: @11;
        height: @11;
        left:-1*@10;
        top:@7;
        &:extend(.icono-stroke);
        border-bottom: none;
        border-left: none;
        transform: rotate(45deg);
        border-radius: 0 @4 0 0;
    }
}


