//*-- Eye --*\\
.icono-eye{
    border-radius: 80% 20%;
    transform: rotate(45deg);
    &:extend(.icono-stroke);
    border-width: @2 @1 @1 @2;
    height: @28;
    width: @28;
    margin: @3 @3;
    &:before{
        border-radius: 50%;
        box-shadow: 0px -1*@3 0 @3 inset;
        height: @11;
        width: @11;
        &:extend(.stickCenter);
    }
}


