//*-- Pin --*\\
.icono-pin{
    width: @26;
    height: @26;
    &:extend(.icono-stroke);
    border-radius: 50% 50% 50% 0;
    transform: rotate(-45deg);
    margin: @1 @4 @7;
    &:before{
        position: absolute;
        width: @6;
        height: @6;
        &:extend(.icono-stroke);
        border-radius: 50%;
        &:extend(.stickCenter);
    }
}


